.backdrop{
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(85, 85, 85, 0.454);
}
.modal-franchise {
    width: 280px;
    max-height: 674px;
    margin: auto;
    background: var(--light-text-color);
    outline: 2px solid var(--brend-color);
    border-radius: 10px;
    padding: 28px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    @include tablet{
        width: 568px;
        padding: 32px 60px;
        max-height: 668px;
    }
}

.close-modal-franchise {
    position: absolute;
    top: 16px;
    right: 16px;
    width: 20px;
    height: 20px;
    background-color: transparent;
    border: transparent;
    // display: flex;
    // align-items: center;
    // justify-content: center;
    @include tablet{
        top: 12px;
        right: 12px;
        width: 24px;
        height: 24px;
    }
}

.modal-franchise-icon {
}

.modal-franchise-title {
    font-family: 'Seymour One';
    @include font(null, 16px, 1.5, null);
    margin-bottom: 20px;
    @include tablet{
    @include font(null, 20px, 1.4, null);
    margin-bottom: 20px;
    width: 322px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    }
}

.modal-franchise-text {
    @include font(400, 14px, 1.4, null);
    margin-bottom: 24px;
    @include tablet{
        @include font(400, 16px, 1.4, null);
        margin-bottom: 32px;
    }
}

.modal-franchise-form {
    text-align: center;
}

.modal-franchise-label {}

.modal-franchise-input {
    text-indent: 12px;
    height: 32px;
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
    margin-bottom: 12px;
    width: 100%;
    outline: none;
    @include tablet{
        height: 42px;
        width: 368px;
        margin-bottom: 16px;
        text-indent: 16px;
    }
}

.modal-franchise-input::placeholder{
    @include font(null, 12px, 1.4, null);
    color: var(--dark-text-color);
    @include tablet{
     @include font(null, 14px, 1.4, null);
    }
}

.modal-franchise-area {
    text-indent: 12px;
    height: 64px;
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
    width: 100%;
    margin-bottom: 24px;
    outline: none;
    @include tablet{
        height: 84px;
        width: 368px;
        margin-bottom: 32px;
        text-indent: 16px;
    }
}

.modal-franchise-area::placeholder{
    @include font(400, 12px, 1.4, null);
    color: var(--dark-text-color);
    padding-top: 8px;
    @include tablet{
    @include font(400, 14px, 1.4, null);
    padding-top: 11px;
    text-indent: 16px;
    }
}

.modal-franchise-button{
    margin: 0 auto;
    padding: 12px 34px;
}